@use '../../mixin/mixin' as *;
@use '../../setting/setting' as *;
/*============
nav
=============*/
.nav {
        display: block;
        position: fixed;
        top: 0;
        left: -300px;
        bottom: 0;
        width: 300px;
        background: #4b6cb7;
        overflow-x: hidden;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
        transition: all 0.5s;
        z-index: 3;
        opacity: 0;
}

.open nav {
  left: 0;
  opacity: 1;
  width: 100vw;
  height: 90vh;
  span {
    display: block;
    font-size: 2rem;
    @include maxmq(sp) {
      font-size: 1.4rem;
    }
  }
}
nav .inner {
  padding: 25px;
  @include maxmq(sp) {
    padding-top: 14px;
  }
  img {
    margin-bottom: 100px;
    width: 200px;
    margin-left: 15px;
    @include maxmq(sp) {
      margin-bottom: 92px;
      width: auto;
      margin-left: 0;
    }
  }
}
nav .inner ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
nav .inner ul li {
  position: relative;
  margin: 0;
  font-size: 3.6rem;
  padding-bottom: 40px;
  @include maxmq(sp) {
    font-size: 2rem;
    padding-bottom: 8px;
    &:first-child {
      padding-bottom: 36px;
    }
  }
  &:hover {
    color: #000;
  }
}
nav .inner ul li a {
  display: block;
  color: #333;
  font-size: 14px;
  padding: 1em;
  text-decoration: none;
  transition-duration: 0.2s;
}
nav .inner ul li a:hover {
  background: #e4e4e4;
}
@media screen and (max-width: 767px) {
  nav {
    left: -220px;
    width: 220px;
  }
// }
/*============
.toggle_btn
=============*/
.toggle_btn {
  display: block;
  position: fixed;
  top: 16px;
  right: 13px;
  width: 28px;
  height: 24px;
  transition: all 0.5s;
  cursor: pointer;
  z-index: 3;
  @include minmq(tab) {
    display: none;
  }
}
.open .toggle_btn {
  display: block;
  position: fixed;
  top: 16px;
  right: 13px;
  width: 28px;
  height: 30px;
  transition: all 0.5s;
  cursor: pointer;
  z-index: 3;
  @include minmq(tab) {
    display: none;
  }
}
.toggle_btn span {
  display: block;
  position: absolute;
  left: 0;
  width: 21px;
  height: 2px;
  background-color: #fff;
  border-radius: 4px;
  transition: all 0.5s;
}
.toggle_btn span:nth-child(1) {
  top: 4px;
}
.toggle_btn span:nth-child(2) {
  top: 11px;
}
.toggle_btn span:nth-child(3) {
  bottom: 4px;
}
.open .toggle_btn span {
  background-color: #fff;
}
.open .toggle_btn span:nth-child(1) {
  -webkit-transform: translateY(10px) rotate(-315deg);
  transform: translateY(10px) rotate(-315deg);
}
.open .toggle_btn span:nth-child(2) {
  opacity: 0;
}
.open .toggle_btn span:nth-child(3) {
  -webkit-transform: translateY(-10px) rotate(315deg);
  transform: translateY(-10px) rotate(315deg);
}
/*============
#mask
=============*/
#mask {
  display: none;
  transition: all 0.5s;
}
.open #mask {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.8;
  z-index: 2;
  cursor: pointer;
}
}